/*index.html-----------------------------------*/

.post-items {
  .post-item {
    width: 100%;
    float: left;
    background-color: $baseColor3;
    
    margin-bottom: 50px;
    box-shadow: 3px 3px 5px 2px $baseColor5;
    border-radius: 5px;
    @include removeDeco();
    transition: transform 0.5s ease-in-out;

    .img-container {
      height: 220px;
      position: relative;
      overflow: hidden;
      .img {
        top: 50%;
        left: 50%;
        position: relative;
        transform: translate(-50%, -50%);
        height: auto;
        width: 100%;
      }
    }

    .text {
      display: block;
      width: 100%;
      max-height: 130px;
      overflow: hidden;
      padding: 7px;
      text-align: center;
      .metadata {
        font-size: 0.8rem;
        text-align: left;
      }

      .title {
        font-size: 2rem;
      }

      .excerpt {
        font-size: 1rem;
      }
    }
    &:hover {
      transform: scale(1.08, 1.08);
    }
  }
  &::after {
    content: "";
    display: block;
    clear: both;
  }
}

